
div.home_background {
  scroll-behavior: smooth;
  margin-top: 15px;
}
.main2 {
  width: 100%;
  color: white;
  align-items: center;
  background-image: url("../../image/IMG_8608-2.jpg");
  background-size: 100%;
  height: 45vw;
  background-repeat: no-repeat;
  background-position-x: center;
  filter: opacity(92%) contrast(110%) grayscale(10%);
}

.main2-txt{
  
}


.main-header {
  opacity: 0;
  transform: translatex(10vw);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  margin-top: 0;
  padding-top: 15vw;
  margin-right: 100px;
  margin-bottom: 0;
  text-align: right;
  color: #fafafa;
  font-size: 3vw;
  margin-left: 20%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 500;
  animation: fade-in 2s;
}

.main-header.is-visible{
  opacity: 1;
  transform: none;
  visibility: visible;
}

.subtitle {
  opacity: 0;
  transform: translatex(10vw);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
  padding-top: 1.5vw;
  margin: 0;
  text-align: right;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 300;
  font-size: 1.5vw;
  animation: fade-in 2s;
}

.subtitle.is-visible{
  opacity: 1;
  transform: none;
  visibility: visible;
}
.subtitle a {
  padding-right: 76px;

  letter-spacing: 1px;
}
@keyframes fade-in {
  from {opacity: 0;}
  to {opacity: 1;}  
}